a, li {
  text-decoration: none;
  color: #ff6060;
  list-style: none;
  font-size: 25px;
}

.ctn-header {
  display: flex;
  justify-content: center;
}

.header {
  text-align: right;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin-bottom: 1em;
  margin-top: 1em;

  img {
    margin-bottom: 1em;
    margin-top: 1em;
    width: 15%;
  }
}

.nav-links {
  display: flex;


  a {
    @media (max-width: 768px) {
      font-size: 15px;
    }

    @media (max-width: 400px) {
      font-size: 12px;
    }
    @media (max-width: 330px) {
      font-size: 10px;
    }
    &:hover { /* Add this block */
      text-decoration: underline; /* Underline the link on hover */
    }
  }
}

.links {
  padding: 10px;
}

/* Media Query pour les petits écrans */
@media (max-width: 768px) {
  .header img {
    width: 40%;
  }
}
@media (max-width: 300px) {
  .header {
  margin: 0;
  img {
    width: 25%;
  }
}
}